.settingsContainer {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  padding: 6%;
  gap: 2%;
}

.settingsContainer > div:not(.gridBlack, .gridWhite) {
  padding: 0%;
  flex-basis: 0;
  flex: 1;
  flex-shrink: 1;
}

.setNotes,
.setScales {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 30px;
}

.settingsContainer input {
  text-align: center;
}

.gridWhite {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 10px;
}

.gridBlack {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 10px;
}

.gridWhite button,
.gridBlack button {
  width: 100%;
  height: 100%;
  align-self: center;
}

.gridBlack button {
  transform: translateX(50%);
}

.theme1,
.theme2,
.theme3 {
  width: 50px;
  height: 20px;
  border-radius: 2px;
  margin-left: 10px;
}

.theme1:hover,
.theme2:hover,
.theme3:hover {
  border: 2px solid var(--color5);
}

.theme1 {
  background: var(--gradient1);
}

.theme2 {
  background: var(--gradient2);
}
.theme3 {
  background: var(--gradient3);
}

.disab,
.disab * {
  pointer-events: none;
  color: grey;
  border-color: grey;
}

.scalesButtons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
  grid-template-rows: auto;
}

.scalesButtons button {
  width: 100%;
  height: 100%;
}
